import React, {Component} from 'react';
import {NavLink} from 'react-router-dom';
import axios from 'axios';
import PureRenderMixin from 'react-addons-pure-render-mixin'
import './order.css'
import Header from '../../component/header/herder'
import Footer from '../../component/footer/footer'
import Left from '../../component/left/left'

class order extends Component {
    constructor(props, context) {
        super(props, context);
        this.shouldComponentUpdate = PureRenderMixin.shouldComponentUpdate.bind(this);
        this.state = {
            data: []
        }
    }

    render() {
        const userOrder = this.state.data;

        return (
            <div className="ysx-order">
                <Header/>
                <Left/>
                <div className="myOrder" >
                    <div className="myOrder-title">
                        
                    </div>
                    <div className="myOrder-select">
                        <select>
                            <option value="orderNumber"></option>
                            <option value="noPay"></option>
                            <option value="dfh"></option>
                            <option value="wfh"></option>
                            <option value="dtk"></option>
                        </select>
                        <input placeholder=""/>
                        <button type="button"></button>

                    </div>
                    <div className="myOrder-list">
                        <div className="myOrder-list-top">
                            <div className="myOrder-list-top-number"></div>
                            <div className="myOrder-list-top-sjr"></div>
                            <div className="myOrder-list-top-state"></div>
                            <div className="myOrder-list-top-price"></div>
                            <div className="myOrder-list-top-time"></div>
                            <div className="myOrder-list-top-cz"></div>
                        </div>
                        <div className="myOrder-list-bottom">
                            {userOrder.map((item, index) => {
                                return <div key={index}className="myOrder-list-template">
                                    <div className="myOrder-list-template-number">
                                        <NavLink to={'/orderDetails/'+item.orderNumber}>
                                            {item.orderNumber}
                                        </NavLink>
                                    </div>
                                    <div className="myOrder-list-template-sjr">{item.name}</div>
                                    <div className="myOrder-list-template-state">{item.orderState}</div>
                                    <div className="myOrder-list-template-price">{item.orderAmount}</div>
                                    <div className="myOrder-list-template-time">{item.orderTime}</div>
                                    <div className="myOrder-list-template-cz">
                                        <NavLink to={'/orderDetails/'+item.orderNumber}></NavLink>
                                    </div>
                                </div>
                            })}
                        </div>
                    </div>
                    <div className="myOrder-page">
                        <div className="">
                            <i className="iconfont icon-left-trangle"></i>
                        </div>
                        <div id="page">1</div>
                        <div className="">2</div>
                        <div className="">3</div>
                        <div className="">...</div>
                        <div className="">188</div>
                        <div className="">
                            <i className="iconfont icon-right-trangle"></i>
                        </div>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }

    componentDidMount() {
        this._getUserOrder();

    };

    _getUserOrder() {
        axios.get("/admUserOrder")
            .then((res) => {
                this.setState({
                    data: res.data
                })
            })
            .catch((err) => {
                console.log(err)
            })
    };

}

export default order;


